body {
  margin: 0;
  font-family: sans-serif;
}

.section {
  height: 100vh;
  background-color: #fab1a0;
  justify-content: center;
  align-items: center;
  display: flex;
}

.envelope {
  width: 200px;
  height: 110px;
  cursor: pointer;
  position: relative;
}

.envelope__top {
  width: 100%;
  transform-origin: bottom;
  z-index: 5;
  transition: all .3s;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(-100%);
}

.envelope.open .envelope__top {
  width: 100%;
  transform-origin: bottom;
  transition: all .2s;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(-100%);
}

.envelope.closed .envelope__top {
  z-index: 2;
  transform: translateY(-110%)rotateX(180deg);
}

.envelope__body {
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.paper {
  width: 100px;
  height: 80px;
  text-align: center;
  background-color: #fff;
  margin-left: -50px;
  transition: all .3s;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateY(-40px);
}

